
body {
  font-family: Helvetica;
  margin: 0;
}
.header {
  height: 40px;
  line-height: 40px;
  background-color: #f0f0f0;
  padding: 0 20px;
& select {
    margin-right: 10px;
}
}
.people {
  margin-top: 20px;
  padding: 0 20px;
}
.person {
  font-size: 1.4rem;
& .name {
    width: 150px;
    display: inline-block;
}
& .meta {
    display: inline-block;
}
}
.timeago {
  color: #999;
}



